.new-expense {
  background-image:url(data:image/jpeg;base64,);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 2px solid white;
  background-color:blue;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: purple;
  border-color: white;
  stroke-dasharray: 50 0;
  stroke-width: 3px;
  stroke-dashoffset: 0;
  stroke: #06D6A0;
}

.new-expense button.alternative {
  color: blue ;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}
.new-expense button:hover,
.new-expense button:focus{
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  box-shadow: 0 0 0 2em transparent;
}
@-webkit-keyframes button{
  0% { box-shadow: 0 0 0 0 var(--hover); }
}
@keyframes button{
  0% { box-shadow: 0 0 0 0 var(--hover); }
}
.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  background-color: white;
  
}
.expense-date{
  border: 1px solid white;
  box-shadow: 2px 3px 12px lightyellow;
  width: 90px;
  height: 100px;
  text-align: center;
  border-radius: 10px;
  background-image: url(data:image/jpeg;base64,);
  background-size: cover;
  background-repeat: no-repeat;
  padding-top:20px;
  color:red;
  font-size: large;
  font-weight: bold;
}

.expense-date__day{
  font-weight:bold;
}


.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px blue;
  padding: 0.5rem;
  margin: 1rem 0 ;
  border-radius: 12px;
  /* background-color:lightcyan; */
  background-image: url(data:image/jpeg;base64,);
  background-repeat: no-repeat;
  background-size: cover;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  color: lightblue;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color:blue;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color:blueviolet;
  border: 1px solid darkblue;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
.image{
  width:30px;
 -webkit-backface-visibility: hidden;
         backface-visibility: hidden;
 border-radius: 50%;
}


.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback {
  color:blue;
  text-align: center;
}
.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 2px solid white ;
  
  background-color:mediumturquoise;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color:blue;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
.chart {
  padding: 1rem;
  border-radius:10px ;
  background-color:lightcyan;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}

.expenses {
  padding: 1rem;
  /* background-color:aquamarine; */
  margin: 2rem auto;
  width: 50rem;
  max-width: 90%;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: no-repeat;
  background-size: cover;
}
