.expense-date{
  border: 1px solid white;
  box-shadow: 2px 3px 12px lightyellow;
  width: 90px;
  height: 100px;
  text-align: center;
  border-radius: 10px;
  background-image: url(../images/pic.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-top:20px;
  color:red;
  font-size: large;
  font-weight: bold;
}

.expense-date__day{
  font-weight:bold;
}
